@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.list {
    margin: 15px 40px 15px 40px;
    padding: 10px; 
    background-color: #F7F7F7;
    border-radius: 15px;
    display:flex; 
    justify-content: space-evenly;
    flex-wrap:wrap;
}
.kasa-thumb {
    background: #FF6060;
    width: 340px;
    height: 340px;
    border-radius: 10px;
    margin: 10px 0px 10px 0px;
    position: relative; 
}
.KasaThumbImage {
    width: 340px; 
    height: 340px;
    border-radius: 10px;
}
.kasa-thumbTitle {
    position: absolute;
    font-family: 'Montserrat';
    color: #FFFFFF;
    bottom: 0px;
    left: 10px; 
    font-size: 13pt;
    width: 40%;
    font-weight: 900;
}
@media screen and (max-width: 425px) {
    .kasa-thumb {
        width: 280px; 
        height: 280px;
    }
    .KasaThumbImage {
        width: 280px; 
        height: 280px;
    }
}
@media  screen and (min-width: 1120px) {
    .kasa-thumb {
        margin: 10px;
    }
}

.image { 
    height: 220px;
    margin: 10px 40px 10px 40px;
    background-image: url(/kasa/static/media/IMG.3d1174c0.svg);
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    text-align: center;
    display: flex;
}
.Simage {
    background-image: url(/kasa/static/media/Simage.a7e59686.svg)
}
.text {
    margin:auto;
    color: white;
    font-family: 'Montserrat';
}
@media screen and (max-width: 768px) {
    .text {
        font-size: 16pt;
    }
}
.kasa-tag {
    background: #FF6060;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 5px 10px 5px 5px; 
    border-radius: 5px;
    font-family:'Montserrat';
    color: white;
    display: flex;
    margin-right: 20px;
}
.tagback{
    display:flex; 
    justify-content: space-around;
}
@media screen and (max-width: 425px) {
    .tagname{
        font-size: 10pt;
    }
}
.drop{
    margin-bottom: 20px;
    width: 100%;
}
.kasa-dropdown {
    margin : 0;
    padding : 0px 10px 5px 10px;
    background-color: #FF6060;
    border-radius: 5px;
   /* width: 400px; */
    display: flex;
    justify-content: space-between;
}
.dropdown-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    display: flex;
    color: #FFFFFF;
}
.Icon {
    padding-top: 5px;
    background-color: transparent;
    border-width: 0px;
}
.dropdownfont {
    color: #FFFFFF;
}
.openDrop {
    background: #F7F7F7;
    border-radius: 0px 0px 10px 10px ;
    width: 250px;
    margin: 0;
    padding: 10px 10px 20px 10px;
    color:#FF6060;
}

.large {
    width: 280px;
    color: #FF6060;
}
@media screen and (min-width: 688px) {
    .drop {
        width: 45%;
    }
    .large {
        width: 100%!important;
        margin: 0px!important;
    }
 }
.carroussel {
    width: auto;
    height: 350px; 
    text-align: center;
    margin: 20px 0px 20px 0px;
    
}
.imageCarroussel {
    position: relative;
    width:90%;
    height: 100%;
    object-fit: cover; 
    border-radius: 20px;
    z-index: 0; 
}
.imageCarroussel:hover {
    cursor : pointer;
}
.right {  
    right: 5%;
}
.left {
    left: 5%;
}
.button {
    color: white; 
    font-size: 60pt;
    background-color: transparent;
    text-shadow: none;
    cursor: pointer;
    border-width: 0px; 
    position: absolute;  
    z-index: 1;
    top: 260px;
}
@media screen and (min-width: 1440px) {
    .button {
        top: 300px;
    }
}
@media screen and (max-width: 425px) {
    .button {
        font-size: 40pt!important;
    }
}
.host {
    font-family: 'Montserrat';
    color: #ff6060;
    display: flex;
    width: 120px;
    margin-top: 20px;
}
.hostName { 
    margin: 0px 10px 0px 0px;
    font-size: 12pt;
    max-width: 70;
}
.hostPhoto {
    width: 40px; 
    height: 40px;
    border-radius: 25px;
    margin-right: 0px;
}
@media screen and (min-width: 688px) {
    .host {
        margin-top: 0;
    }
}

.location {
    font-family: "Montserrat";
    color: #FF6060;
    font-size: 12pt;
    margin: 0px;
}
@media screen and (max-width: 425px) {
    .location {
        font-size: 10pt; 
    }
}
.full {
    color: #FF6060;
    margin-left: 5%;
}
.empty {
    color: #E3E3E3;
    margin-left: 5%;
    margin-top: 30%;
}
@media screen and (max-width: 686px) {
    .empty{
        margin-top : 15px!important;
        margin-left: 0px;
    }
    .full {
        margin-left: 0px;
    }
}
.title {
    font-family: "Montserrat";
    color: #FF6060;
    margin: 0; 
    font-size: 20pt;
}

@media screen and (max-width: 425px) {
    .title {
        font-size: 16pt;
    }
}
@media screen and (min-width: 590px) {
    .title {
        Width: 499px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
@media screen and (min-width: 730px) {
    .title {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
}
@media screen and (max-width: 920px) {
    .title {
        font-size: 14.7pt;
    }
}

.ficheContent {
    margin:0% 5% 5% 5%;
}
/******************************/
.informations {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
/******************************/

.tag {
    margin-top: 15px;
    display: flex;
}
/******************************/
.dropdown {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 5%;
}
@media screen and (min-width: 688px) {
    .dropdown {
        display: flex !important;
    }
}
.apropos {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.kasa-header {
    display: flex; 
    justify-content: space-between;
    margin: 2% 5% 2% 5%;
}
.nav {
    display: flex; 
    justify-content: space-around;
    text-decoration: none;
}
.element {
    margin : 0px 0px  10px 20px; 
    display: flex;
    align-items: flex-end;
    text-align: right;
}
.logo {
    width: 150px; 
}
.lien {
    text-decoration: none;
    color: #FF6060;  
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 24px;
    padding-bottom: 10px;
    
}
@media screen and (max-width: 375px) {
    .kasa-header {
        margin: 5px!important;
        justify-content: space-between;
        width: 300px;
    }
    .lien {
        font-size: 10pt;
        width: 55px;
    }
}
@media screen and (max-width: 450px) {
    .kasa-header {
        margin: 10px;
        justify-content: space-between;
    }
    .lien {
        font-size: 12pt;
        width: 75px;
    }
}
.kasa-footer {
    background-color: black!important;
    padding: 40px 0px 40px 0px;
    margin-top: 50px;
    text-align: center;
    width: 100%;
    flex:1 1;
}
.element-footer {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 20px;
    text-align: center;
    color: #FFFFFF;
}

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    margin: 0;
}
.errorImage {
    width: 350px;
}
.error {
    display:flex;
    flex-direction: column;
    align-items: center;
}
.errorMessage {
    color: #FF6060;
    font-family: 'Montserrat';
    margin: 5% 0% 0% 0%;
}
.main {
    display: flex; 
    flex-direction: column;
    justify-content: center;
}
