.kasa-tag {
    background: #FF6060;
    width: fit-content;
    padding: 5px 10px 5px 5px; 
    border-radius: 5px;
    font-family:'Montserrat';
    color: white;
    display: flex;
    margin-right: 20px;
}
.tagback{
    display:flex; 
    justify-content: space-around;
}
@media screen and (max-width: 425px) {
    .tagname{
        font-size: 10pt;
    }
}